"use client";

import React, { useEffect, useState } from "react";
import { db } from "@/firebase";
import { collection, getDocs } from "firebase/firestore";

export default function PartialCustomersPage() {
  const [orders, setOrders] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchOrders = async () => {
      try {
        const querySnapshot = await getDocs(collection(db, "orders"));
        const data = querySnapshot.docs.map((doc) => ({
          id: doc.id,
          ...doc.data(),
        }));

        // Filter only partial payment customers
        const partial = data.filter(
          (order) => order.paymentType === "partial" || order.paymentType === "installment"
        );

        setOrders(partial);
        setLoading(false);
      } catch (error) {
        console.error("Error fetching data:", error);
        setLoading(false);
      }
    };

    fetchOrders();
  }, []);

  const getNextPaymentWarning = (nextPaymentDate) => {
    if (!nextPaymentDate) return "";

    const today = new Date();
    const next = new Date(nextPaymentDate);

    const diff = Math.ceil((next - today) / (1000 * 60 * 60 * 24));

    if (diff <= 3 && diff >= 0) {
      return `ðŸ”´ Payment in ${diff} day(s)!`;
    }

    return "";
  };

  if (loading) {
    return (
      <div className="p-8 text-center text-xl font-semibold">
        Loading...
      </div>
    );
  }

  return (
    <div className="p-8">
      <h1 className="text-3xl font-bold mb-6">Partial Payment Customers</h1>

      {orders.length === 0 ? (
        <p className="text-lg">No partial customers found.</p>
      ) : (
        <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
          {orders.map((order) => (
            <div
              key={order.id}
              className="border rounded-xl p-6 shadow-lg bg-white"
            >
              <h2 className="text-xl font-bold mb-2">{order.customerName}</h2>

              <p><strong>Phone:</strong> {order.phone}</p>
              <p><strong>Product:</strong> {order.productName}</p>
              <p><strong>Total Cost:</strong> {order.cost} so'm</p>
              <p><strong>Monthly Payment:</strong> {order.monthlyPayment} so'm</p>

              <p><strong>Start Date:</strong> {order.paymentStartDate}</p>
              <p><strong>Next Payment:</strong> {order.nextPaymentDate}</p>

              <p className="mt-3 text-red-600 font-semibold">
                {getNextPaymentWarning(order.nextPaymentDate)}
              </p>
            </div>
          ))}
        </div>
      )}
    </div>
  );
}